// Source Sans pro - variable
@font-face {
    font-family: 'Source Sans Pro';
    src: url(../assets/webfonts/static/SourceCodePro-VariableFont_wght.ttf)
    format('truetype-variations');
    font-weight: 200 900;
    font-display: swap;
}
@font-face {
    font-family: Cinzel;
    src: url(../assets/webfonts/static/Cinzel-VariableFont_wght.ttf)
    format('truetype-variations');
    font-weight: 200 900;
    font-display: swap;
}
